.Movie {
  width: 100%;
  overflow: hidden;
  background-color: rgb(221, 221, 221);
  color: white;
  min-height: 11.5em;
  transition: all 0.4s ease;
  border-radius: 15px;
}

.Movie:hover {
  transform: translateY(-10px);
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

.Movie img {
  width: 7em;
  position: absolute;
  transform: translate(1.225em, 1.225em);
  box-shadow: 0.3125em 0.625em 0.3125em rgb(51, 51, 51);
}

.Wrap {
  padding: 0.625em 0.625em 0.625em 9.5625em;
}

.Title {
  font-size: 1.125em;
  font-weight: 700;
  color: black;
  margin-bottom: 0.125em;
}

.Genre {
  font-size: 1em;
  color: gray;
  margin-left: -0.3125em;
}

.Summary {
  font-size: 1rem;
  line-height: 1.2rem;
  color: black;
  margin: 0.8rem 0;
}
